import React from "react";
import { Link, Outlet, useLocation } from "react-router-dom";
import { Layout, Flex, Typography, Menu, Row, Col, theme } from "antd";
//  React Icons (https://react-icons.github.io/react-icons)
// : MIT
import { FaTelegramPlane, FaVk, FaWhatsapp, FaEnvelope } from "react-icons/fa";
import { ReactComponent as PharmLogo } from "../../svg/PharmLogo.svg";

const { Header, Content, Footer } = Layout;

const LayoutCustom = () => {
  const {
    token: { borderRadiusLG },
  } = theme.useToken();

  const { Text, Title } = Typography;

  // const [form] = Form.useForm();

  // const onFinish = (values) => {
  //   console.log("Received values:", values);
  // };

  const location = useLocation();

  return (
    <Layout className="mainBack" style={{ height: "100%", background: "none" }}>
      <Header
        style={{
          justifyContent: "center",
          display: "flex",
          background: "none",
          alignItems: "center",
          paddingLeft: "10vw",
          paddingRight: "10vw",
          paddingTop: "10rem",
          paddingBottom: "10rem",
          minHeight: "90rem", // ?
          // boxShadow: "0px 3px 2px -1px rgba(34, 60, 80, 1)",
        }}
      >
        <PharmLogo
          // width={150}
          // height={80}
          style={{
            background: "none",
            marginRight: "50",
            minHeight: "40",
            minWidth: "40",
            width: "150rem",
            height: "80rem",
          }}
        />
        <Menu
          id="Menu"
          theme="light "
          mode="horizontal"
          selectedKeys={[location.pathname]}
          style={{
            // fontSize: "0.9vw",
            fontWeight: "600",
            flex: 1,
            minWidth: 0,
            maxWidth: "900rem",
            lineHeight: "60rem",
            borderRadius: "40rem",
            background: "none",
            justifyContent: "center",
            boxShadow: "0px 0px 20px -12px rgba(34, 60, 80, 1)",
          }}
        >
          <Menu.Item key="/">
            <Link to="/"></Link>
          </Menu.Item>
          <Menu.Item key="/about">
            <Link to="/about"> </Link>
          </Menu.Item>
          <Menu.Item key="/protezirovanie"></Menu.Item>
          <Menu.Item key="/ortezirovanie"></Menu.Item>
          <Menu.Item key="/infprotez"> </Menu.Item>
          <Menu.Item key="/contact">
            <Link to="/contact"></Link>
          </Menu.Item>
        </Menu>
      </Header>
      <Content
        style={{
          // padding: "0 2vw",
          padding: "0",
        }}
      >
        <div
          style={{
            background: "none",
            minHeight: 1080,
            // padding: "2vw",
            borderRadius: borderRadiusLG,
          }}
        >
          <Outlet />
        </div>
      </Content>
      <Footer
        className="footerCss"
        style={{
          textAlign: "center",
          color: "#1677ff",
          background: "",
          // padding: "20px 50px",
          padding: "0",
        }}
      >
        <Row className="gutter-box-footer">
          <Row
            justify="space-between"
            align="middle"
            style={{
              gap: "30px",
              zIndex: "10",
              width: "100%",
              padding: "20px 50px",
            }}
          >
            <Col>
              <Title level={4} style={{ color: "#1677ff" }}>
                -
              </Title>
              <Text style={{ color: "white" }}>
                 {new Date().getFullYear()} Pharm-rost.   .
              </Text>
            </Col>
            <Flex gap={20}>
              <FaTelegramPlane
                id="hoverIcon"
                style={{ fontSize: "30rem", color: "rgb(255 255 255)" }}
              />
              <FaVk id="hoverIcon" style={{ fontSize: "30rem" }} />
              <FaWhatsapp
                id="hoverIcon"
                style={{ fontSize: "30rem", color: "rgb(37, 211, 102)" }}
              />
              <FaEnvelope
                id="hoverIcon"
                style={{ fontSize: "30rem", color: "rgb(255 255 255)" }}
              />
            </Flex>
          </Row>
        </Row>
      </Footer>
    </Layout>
  );
};

export default LayoutCustom;
