import React from "react";
import { Col, Row } from "antd";

import Sert1 from "../../svg/ertificateIMG/Sert1.jpg";
import Sert2 from "../../svg/ertificateIMG/Sert2.jpg";
import Sert3 from "../../svg/ertificateIMG/Sert3.jpg";

const ertificate = () => {
  // const containerStyle = {
  //   display: "flex",
  //   flexWrap: "wrap",
  //   justifyContent: "space-between",
  //   position: "relative",
  // };

  const imageStyle = {
    borderRadius: "20rem",
    border: "0.5rem solid black",
    width: "100%",
    height: "300rem",
    objectFit: "cover",
    position: "relative",
    filter: "brightness(0.5)",
  };

  const fullWidthImageStyle = {
    borderRadius: "20rem",
    border: "0.5rem solid black",
    width: "100%",
    height: "300rem",
    objectFit: "cover",
    position: "relative",
    filter: "brightness(0.5)",
  };

  const textStyle = {
    position: "absolute",
    // color: "#FFFFFF",
    // fontSize: "24rem",
    // textAlign: "center",
    justifyContent: "center",
    gap: "20rem",
    top: "50%",
    left: "50%",
    transform: "translate(-50%, -50%)",
  };

  const textTitle = {
    fontSize: "24rem",
    textAlign: "center",
    color: "rgb(211 211 211)",
    fontWeight: "700",
  };

  const textContent = {
    fontSize: "20rem",
    textAlign: "center",
    color: "#FFFFFF",
  };

  return (
    <Row style={{ marginLeft: "5rem", marginRight: "5rem" }}>
      <Col xs={24} sm={12} style={{ position: "relative", padding: "1%" }}>
        <img className="imgMediaSert" src={Sert1} alt="1" style={imageStyle} />
        <Row style={textStyle}>
          <Row style={textTitle}> </Row>
          <Row style={textContent}>
                   
            .     ,   
               .
          </Row>
        </Row>
      </Col>
      <Col xs={24} sm={12} style={{ position: "relative", padding: "1%" }}>
        <img className="imgMediaSert" src={Sert2} alt="2" style={imageStyle} />
        <Row style={textStyle}>
          <Row style={textTitle}>  :</Row>
          <Row style={textContent}>
                   
            .     ,   
               .
          </Row>
        </Row>
      </Col>
      <Col xs={24} style={{ position: "relative", padding: "1%" }}>
        <img
          className="imgMediaSert"
          src={Sert3}
          alt="3"
          style={fullWidthImageStyle}
        />
        <Row style={textStyle}>
          <Row style={textTitle}> </Row>
          <Row style={textContent}>
                   
            .     ,   
               .
          </Row>
        </Row>
      </Col>
    </Row>
  );
};

export default ertificate;
