// import { Context } from "./components/Contex";
// import RouterCustom from "./components/RouterCustom/RouterCustom";
import { BrowserRouter as Router, Route, Routes } from "react-router-dom";
import { ConfigProvider } from "antd";
import LayoutCustom from "./components/Pages/LayoutCustom";
import Home from "./components/Pages/Home";
import About from "./components/Pages/About";
import Contact from "./components/Pages/Contact";

export const App = () => {
  // const [data, setData] = useState(null);
  return (
    // <Context.Provider value={{ data, setData }}>
    //   <Router />
    // </Context.Provider>
    // <RouterCustom />
    <ConfigProvider
      theme={{
        token: {
          fontSize: "16rem",
          colorPrimary: "rgb(41, 91, 112)",
          // padding: "0",
        },
      }}
    >
      <Router>
        <Routes>
          <Route path="/" element={<LayoutCustom />}>
            <Route index element={<Home />} />
            <Route path="about" element={<About />} />
            <Route path="contact" element={<Contact />} />
          </Route>
        </Routes>
      </Router>
    </ConfigProvider>
  );
};
